import { createStore } from "vuex";
import{ ref } from "vue";
import Web3 from "web3";
import HoldMoneyJson from "@/contract/bin/holdMoney.json"
import MultiSigWallet from "@/contract/bin/MultiSigWallet.json"


import { message } from 'ant-design-vue'
const { messageApi } = message.useMessage();

const HoldMoneyAbi = HoldMoneyJson.abi;
const HoldMoneyAddress = ref(undefined);

export default createStore({
  state: {
    web3: null,
    contract: null,
    // deployedContractAddress: undefined, // 已部署的合约地址
    approvers: [], // 批准者地址列表
    approvalsRequired: 0,  // 所需批准数量
    budget: 0  // 初始预算
  },
  mutations: {
    // 设置web3实例
    setWeb3(state, web3) {
      state.web3 = web3;
    },
    // 设置合约实例
    setContract(state, contract) {
      state.contract = contract;
    },
    setApprovers(state, approvers) {
      state.approvers = approvers;
    },
    setApprovalsRequired(state, approvalsRequired) {
      state.approvalsRequired = approvalsRequired;
    },
    setBudget(state, amount) {
      state.budget = amount;
    },
  },
  actions: {
    async initWeb3() {
      if(window.ethereum) {
        try{
          //请请求用户授权账户
          const accounts = await window.ethereum.request({ method: 'eth_requestAccounts' });
          console.log("account: "+accounts);
          //创建web3对象
          const web3 = new Web3(window.ethereum);
          // 设置provider
          web3.setProvider(window.ethereum);
          // 设置默认账户为第一个账户
          web3.eth.defaultAccount = accounts[0];
          // 创建合约实例, 做准备
          const contract = new web3.eth.Contract(HoldMoneyAbi);

          // 为估算gas部署新合约
          const estimatedGas = await contract.deploy({
            data: HoldMoneyJson.bytecode,
            budget: this.budget,
          }).estimateGas({
            from: web3.eth.defaultAccount,
          });
          // 再部署一次
          const deployedContract = await contract.deploy({
            data: HoldMoneyJson.bytecode,
            budget: this.budget,
          }).send({
            from: web3.eth.defaultAccount,
            gas: Math.round(estimatedGas * 1.1),
          });

          const deployedContractAddress = deployedContract.options.address;
          console.log("合约已部署到地址: ",deployedContractAddress); 
          return {web3, contract:deployedContract};
        }catch (error) {
          // 处理用户拒绝或其他错误
          console.error(error+'用户拒绝或有其他错误');
        }
      }else {
        messageApi.error('请安装metamask插件')
      }
    },
    // 设置初始预算; 
    async setInitialBudget({ state, commit }, amount) {
      try {
        if(!state.web3 || !state.contract) {
          throw new Error("Web3或合约实例尚未初始化");
        }
        // 调用合约方法设置初始预算
        await state.contract.methods.addBudget(amount).send({ from: state.web3.eth.defaultAccount });
        commit('setBudget', amount);
      } catch(error) {
        console.error("设置初始预算出错：", error);
        throw error;
      }
    },
    // 添加签名者的接口
    async addApprover({ state,dispatch,commit }, approverAddress) {
      const { contract, web3 } = state;
      try {
        if(!state.web3 || !state.contract) {
         
          const { web3, contract } = await dispatch('initWeb3');
          commit('setWeb3', web3);
          commit('setContract', contract);

          throw new Error('web3或contract未初始化');
        }
        await contract.methods.pushPoweFulMan(approverAddress).send({ from: web3.eth.defaultAccount });
        // dispatch('fetchApprovers');
      }catch (error) {
        console.error("添加所有者出错",error);
      }
      // const accounts = await web3.eth.getAccounts();
      // await contract.methods.addApprover(approverAddress).send({ from: accounts[0] });
    },
    // 获取签名者列表
    // async fetchApprovers({ state, commit }) {
    //   try {
    //     if(!state.web3 || !state.contract) {
    //       throw new Error('web3或contract未初始化');
    //     }
    //     const { contract } = state;
    //     const approvers = await contract.methods.getApprovers().call();
    //     commit('setApprovers', approvers);
    //     return approvers;
    //   }catch (error) {
    //     console.error("获取所有者出错",error);
    //   }
    // },
    // 减少签名者的接口
    async removeApprover({ state, dispatch, commit }, approverAddress) {
      try {
        if(!state.web3 || !state.contract) {
          throw new Error('web3或contract未初始化');
        }
        const { contract, web3 } = state;
        await contract.methods.popPoweFulMan(approverAddress).send({ from: web3.eth.defaultAccount });
        dispatch('fetchApprovers');
      }catch (error) {
        console.error("删除所有者出错",error);
      }

    },
    async createWallet({state,commit},approvalsRequired, DataViewPermissionLevel) {
      try {
        if(!state.web3 || !state.contract) {
          throw new Error("Web3或合约实例尚未初始化");
        }
        const accounts = await this.web3.eth.getAccounts();
        const receipt = await this.contract.methods.createContract(approvalsRequired, DataViewPermissionLevel).send({
            from: accounts[0] // 使用第一个账户作为发送方
        });
        console.log("新的MultiSigWallet合约已创建, 交易收据:", receipt);
      }catch(error) {
        console.error("创建钱包出错：", error);
        throw error;
      }
    }
  }
});
